div.m_navbar {
  z-index: 1000;
  width: 100%;
  background-color: var(--background);
  box-shadow: var(--shadow-small);
  flex-direction: row;
  justify-content: center;
  display: flex;
}

div.m_navbar nav {
  width: var(--content-width);
  height: 80px;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
}

div.m_navbar nav > input {
  position: absolute;
  left: -999em;
}

div.m_navbar nav > label {
  color: var(--foreground);
  display: block;
}

div.m_navbar nav > ul {
  list-style: none;
  display: none;
}

div.m_navbar nav .menu-item {
  text-transform: uppercase;
  color: #000;
  padding: 0;
  font-weight: 400;
  line-height: 1.25rem;
  text-decoration: none;
}

div.m_navbar nav > a {
  text-transform: uppercase;
  text-decoration: none;
}

div.m_navbar nav > a span {
  color: #000;
  font-size: 1.5em;
  font-weight: 600;
}

div.m_navbar nav .hamburger {
  cursor: pointer;
}

div.m_navbar nav .hamburger > div {
  width: 2em;
  height: 5px;
  background-color: #000;
  border-radius: 1px;
  margin-top: 6px;
  margin-bottom: 6px;
  transition: all .3s ease-in-out;
  position: relative;
}

@media (max-width: 599px) {
  #toggle:checked + .hamburger .top_bun {
    transform: rotate(-45deg);
  }

  #toggle:checked + .hamburger .bottom_bun {
    margin-top: -22px;
    transform: rotate(45deg);
  }

  #toggle:checked + .hamburger .meat {
    opacity: 0;
    transform: rotate(45deg);
  }

  #toggle:checked ~ ul {
    background-color: var(--background);
    z-index: 100;
    margin: 0;
    padding: 1em;
    display: block;
    position: absolute;
    inset: 80px 0 0;
  }

  #toggle:checked ~ ul > li {
    width: 100%;
