    padding-top: 1em;
    padding-bottom: 1em;
    font-size: .875rem;
    font-weight: 500;
  }

  #toggle:checked ~ ul > li > a {
    font-size: 1.4rem;
  }

  #toggle:checked ~ ul > li.sub-menu-item {
    border-top: none;
    padding-top: .5em;
    padding-bottom: .5em;
  }

  #toggle:checked ~ ul > li.sub-menu-item > a {
    padding-left: 1rem;
    font-size: .9rem;
  }

  #toggle:checked ~ ul > li.sub-menu-item:last {
    padding-bottom: 1rem;
  }

  #toggle:checked ~ ul > li.developer-docs {
    border-top: none;
    padding: 0 0 1em 2em;
  }
